		toLogon: function (content, payload) {
			let _this = this;
			return new Promise(function (resolve, reject) {
				// uniapp
				uni.login({
					provider: 'weixin', //
					success: function (loginRes) { // loginRescode
						// console.log('loginRes', loginRes);
						let that = _this;
						let code = loginRes.code;
						// 
						uni.request({
							url: content.state.theUrl + '/wechat/wx/login',
							// url:'https://school.izekai.cn/wechat/wx/login',
							method: 'POST',
							data: {
								//  code1-2-0-
								code: code
								// code: 2 
							},
							success: (res) => {
								let __that = that;
								// console.log('toLogon_res',res);
								// token  
								if (res.data.code == 0) {
									content.commit('changeTheLogonUser', {
										theLogonUser: res.data.data.user,
										theToken: res.data.data.token
									})
									// console.log('theLogonUser',content.state.theLogonUser);
									// console.log('theToken',content.state.theToken);

									resolve();
								} else if (res.data.code == 500) {
									uni.showToast({
										title: '~',
										duration: 2500,
										icon: 'none'
									})
								} else {
									uni.showToast({
										title: res.data.msg,
										duration: 2500,
										icon: 'none'
									})
								}
							},
							fail: (res) => {
								uni.showToast({
									title: '',
									duration: 2500,
									icon: 'none'
								})
							}
						})
					},
					fail: (res) => {
						uni.showToast({
							title: '',
							duration: 2500,
							icon: 'none'
						})
					}
				});
			})
		},

